


































































import Vue from "vue";
import store from "./store";

import FirstRun from "@/views/FirstRun.vue";
import NavigationPane from "@/components/site/NavigationPane.vue";
import SiteAuthHeader from "@/components/auth/SiteAuthHeader.vue";
import RegionHeader from "@/components/region/RegionHeader.vue";
import Loader from "@/components/util/Loader.vue";
import Axios from "axios";
import { Actions } from '@/storemutations';

import * as Globals from "./globals";

export default Vue.extend({
    name: "app",
    components: {
        FirstRun,
        NavigationPane,
        SiteAuthHeader,
        RegionHeader,
        Loader,
    },
    data() {
        return {
            isLoading: true,
            started: false,
            showLoginError: false,
            showAuthError: false,
        };
    },
    created() {
        const localInfo = JSON.parse(localStorage.getItem('user_info') || '{}');
        //console.log('localInfo: ', localInfo);
        // this.$route
        //console.log('this.$route: ', this.$route);
        if(window.location.pathname === '/') {
            // 首页不阻塞加载
            this.isLoading = false;
        }
        // 设置数据接口的全局拦截器，报错后显示报错弹窗
        Globals.ApiHttpClient.interceptors.response.use(
            (response) => {
                if(response.data.code === 500) {
                    // 这里先清空localStorage的userInfo
                    localStorage.removeItem('user_info');
                    this.showLoginError = true;
                }
                else if(response.data.code === 402) {
                    // 这里先清空localStorage的userInfo
                    localStorage.removeItem('user_info');
                    this.showAuthError = true;
                }
                return response;
            },(error) => {
            // 返回接口返回的错误信息
                // 这里先清空localStorage的userInfo
                localStorage.removeItem('user_info');
                this.showLoginError = true;
                return Promise.reject(error.response);  
            }
        );
        const timeNow = Math.floor(Date.now()/1000);
        // 如果localInfo不为空,且距离登陆时间不超过7天,直接使用本地缓存的token
        if(localInfo && localInfo.loginTime && timeNow - localInfo.loginTime < 60*60*24*7) {
            // 使用token设置ApiHttpClient的header
            Globals.ApiHttpClient.defaults.headers['Authorization'] = localInfo.token;
            // 保存到store
            this.$store.dispatch(Actions.SetUserInfo, localInfo);
            this.isLoading = false;
        } else {
            // 否则重新请求login接口
            // 创建单独的Axios实例用于请求主站Login
            const loginAxios = Axios.create();
            // 只在build时加上baseUrl
            if(process.env.NODE_ENV === 'production') {
                loginAxios.defaults.baseURL = 'https://dngamer.site';
            }
            loginAxios.get(`/dnwiki/api/login.php`, {
                withCredentials: true
            })
                .then((data) => {
                    if (data.data.code === 200) {
                        //console.log('登陆成功: ', data.data);
                        this.isLoading = false;
                        // 添加登陆时间
                        let userInfo = data.data;
                        userInfo.loginTime = Math.floor(Date.now()/1000);
                        // 保存到store
                        this.$store.dispatch(Actions.SetUserInfo, userInfo);
                        // 保存到localStorage
                        localStorage.setItem("user_info", JSON.stringify(userInfo));
                        // 使用token设置ApiHttpClient的header
                        Globals.ApiHttpClient.defaults.headers['Authorization'] = userInfo.token;
                    } else if (data.data.code === 402) {
                        //console.log('用户无访问权限：', data.data);
                        // 显示无权限错误弹窗
                        this.showAuthError = true;
                    } else {
                        //console.log('未登录：', data.data);
                        window.location.href = "https://dngamer.site/login";
                    }
                })
                .catch((e) => {
                    window.location.href = "https://dngamer.site/login";
                });
        }
    },
    computed: {
        embedded(): boolean {
            return !!this.$route.query.embed;
        },
        showFirstRun(): boolean {
            return !this.embedded && this.$store.getters.showFirstRun && !this.isRedeemingCode;
        },
        isRedeemingCode(): boolean {
            return this.$route.matched.some((r) => r.meta.redemption);
        },
        showRouteLoader(): boolean {
            return this.$store.state.pageLoading;
        },
        hideAuth(): boolean {
            return this.$route.meta.redemption;
        },
        useInstanceText(): string {
            return process.env.VUE_APP_USE_STATIC_SERVER || '';
        }
    },
    // watch: {
    //     $route: {
    //         handler: function(to): void {
    //             // 第一次登陆成功之后,store设置userInfo,不重复请求
    //             if (this.$store.getters.userInfo) return;
    //             Axios.defaults.withCredentials = true;
    //             Axios.get(`https://dngamer.site/dnwiki/api/login.php`)
    //                 .then((data) => {
    //                     if (data.data.code === 200) {
    //                         this.isLoading = false;
    //                         this.$store.dispatch(Actions.SetUserInfo, data.data);
    //                     } else {
    //                         window.location.href = "https://dngamer.site/login";
    //                     }
    //                 })
    //                 .catch((e) => {
    //                     window.location.href = "https://dngamer.site/login";
    //                 });
    //         },
    //         immediate: true,
    //     },
    // },
    mounted() {
        const shade = document.getElementById("startup-shade");
        if (shade) {
            window.setTimeout(() => {
                shade.classList.add("hidden");
            }, 500);
        }

        this.started = true;
    },
    methods: {
    }
});
